@import url('https://fonts.googleapis.com/css2?family=Electrolize&family=Noto+Sans+JP:wght@100..900&family=Roboto+Mono:ital,wght@0,100..700;1,100..700&display=swap');

body {
  background-color: black;
  color: white;
  padding-bottom: 50px;
  font-family: "Electrolize", sans-serif;
  font-family:
}

a:link {
  color: #cccccc;
}

a:visited {
  color: #999999;
}

.Home {
  width: 100%;
  text-align: center;
}

.Search {
  width: 90%;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 30px;
}

.Search input {
  background-color: black;
  color: white;
  font-size: 10pt;
  width: 20%;
  margin-left: 3px;
}

.MapContainer {
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 100px;
  width: 90%;
}

.MapContainer table {
  width: 100%;
  table-layout: fixed;
}

.MapInfo {
  width: 100%;
  height: 35vh;
  background-color: white;
  color: black;
}

.MapTitle {
  width: 90%;
  margin-left: auto;
  margin-right: auto;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.MapImage {
  height: 88%;
  overflow: hidden;
}

.MapImage img {
  display: block;
  object-fit: cover;
  width: 100%;
  height: 100%;
}

img[src='/missing.png'] {
  image-rendering: pixelated;
}
